<template>
  <div class="addGood mescroll">
    <div class="mid">
      <div class="headPic">
        <img class="pic" :src="detail.gimg?$utils.dealImg(detail.gimg):require('@/assets/img/default/defaultH.png')" alt />
        <img class="icon" src="@/assets/img/merchantsIcon/edit.png" @click="upload('1')" alt />
        <input ref="upload1" type="file" accept="image/*" class="file" style="display:none" @change="chooseUpload" />
      </div>
      <van-cell-group>
        <van-field v-model="detail.gname" input-align="right" clearable type="textarea" row="2" label="" placeholder="" @blur="blur()" />
        <van-field v-model="detail.brandName" input-align="right" clearable label="" placeholder=" " @blur="blur()" />
        <van-field v-model="detail.guigeName" input-align="right" clearable label="" placeholder="" @blur="blur()" />
        <van-field v-model="detail.gmarketPrice" input-align="right" clearable type="number" label="" placeholder="" @blur="blur()" />
        <van-field v-model="detail.goutPrice" input-align="right" clearable type="number" label="" placeholder="" @blur="blur()" />
        <van-field v-model="detail.gcounts" input-align="right" clearable type="digit" label="" placeholder="" @blur="blur()" />
        <van-field v-model="detail.gsort" input-align="right" clearable type="digit" label="" placeholder="99" @blur="blur()" />
        <van-field autosize :rows="2" v-model="detail.gdes" input-align="right" label="" placeholder="" type="textarea" clearable maxlength="50" show-word-limit @blur="blur()" />
        <van-cell title="" is-link :value="detail.cateName ||''" @click="cateShow = true" />
        <van-cell title="" is-link :value="detail.unitName||''" @click="unitShow = true" />
        <div>
          <van-cell @click="upload('2')">
            <template slot="title">
              <span class="custom-text"></span>
            </template>
            <div class="content">
              <p></p>
              <img class="icon" src="@/assets/img/merchantsIcon/fujian.png" alt />
              <input ref="upload2" type="file" accept="image/*" class="file" style="display:none" @change="chooseUpload" />
            </div>
          </van-cell>
          <div class="filesList" v-if="picList.length">
            <div v-for="(item,index) in picList" :key="index" class="filesItem">
              <img :src="$utils.dealImg(item)" alt />
              <span class="del" @click="delImg(index)"></span>
            </div>
          </div>
        </div>
      </van-cell-group>
      <div class="submit-wrap">
        <div class="submit" @click="submit()"></div>
      </div>
    </div>
    <van-popup v-model="cateShow" position="bottom">
      <van-picker show-toolbar :columns="cateList" value-key="cateName" @cancel="cateShow = false" @confirm="cateConfirm" />
    </van-popup>
    <van-popup v-model="unitShow" position="bottom">
      <van-picker show-toolbar :columns="unitList" value-key="dictLabel" @cancel="unitShow = false" @confirm="unitConfirm" />
    </van-popup>
  </div>
</template>
<script>
import { CellGroup, Cell, Field, Popup, Picker } from "vant";
import { getShopCateList, getUnitList, editShopGood } from "@/api";
import areaList from "@/utils/AreaList";
export default {
  components: {
    [CellGroup.name]: CellGroup,
    [Cell.name]: Cell,
    [Field.name]: Field,
    [Popup.name]: Popup,
    [Picker.name]: Picker
  },
  data() {
    return {
      type: "",
      detail: {
        gdes: ""
      },
      cateShow: false,
      cateList: [],
      unitShow: false,
      unitList: [],
      picList: [],
      type: ""
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    cateConfirm(value, index) {
      this.detail.cateName = value.cateName;
      this.detail.cateCode = value.cateCode;
      this.cateShow = false;
    },
    unitConfirm(value, index) {
      this.detail.unitName = value.dictLabel;
      this.detail.unitCode = value.dictCode;
      this.unitShow = false;
    },

    delImg(index) {
      this.picList.splice(index, 1);
    },
    upload(type) {
      this.type = type;
      if (this.type == 2 && this.picList.length > 8) {
        this.$toast("9");
        return;
      }
      this.$refs["upload" + this.type].click();
    },
    chooseUpload(e) {
      let that = this,
        formData = new FormData(),
        files = e.target.files,
        reader = new FileReader();
      if (files.length < 1) {
        return;
      }
      this.loading = this.$toast.loading({
        duration: 0,
        forbidClick: true,
        message: "..."
      });
      for (let i = 0; i < files.length; i++) {
        console.log(files[i]);
        let size = files[i].size / 1024 / 1024;
        if (size > 100) {
          // 2M
          this.$toast.fail("100M");
          return;
        }
        reader.readAsDataURL(files[i]);
        reader.onload = function() {
          let content = this.result; //srcbase64
          let img = new Image();
          img.src = content;
          img.onload = function() {
            //
            that.compressEvent(img, size, dataURL => {
              that.uploadImg(dataURL, files[i], i, files.length);
            });
          };
        };
      }
    },
    compressEvent(img, size, callback) {
      let that = this,
        canvas = document.createElement("canvas"),
        ctx = canvas.getContext("2d"),
        initSize = img.src.length,
        width = img.width,
        height = img.height;
      // var ratio = this.getPixelRatio(ctx);
      // console.log(width * ratio, height * ratio);
      canvas.width = width;
      canvas.height = height;
      ctx.drawImage(img, 0, 0, width, height);
      let dataURL = canvas.toDataURL("image/jpeg", size > 1 ? 0.1 : 1);
      callback ? callback(dataURL) : null; //
    },
    getPixelRatio(context) {
      var backingStore =
        context.backingStorePixelRatio ||
        context.webkitBackingStorePixelRatio ||
        context.mozBackingStorePixelRatio ||
        context.msBackingStorePixelRatio ||
        context.oBackingStorePixelRatio ||
        context.backingStorePixelRatio ||
        1;
      return (window.devicePixelRatio || 1) / backingStore;
    },
    dataURItoBlob(dataurl) {
      //dataurlbase64
      var arr = dataurl.split(","),
        mime = arr[0].match(/:(.*?);/)[1],
        bstr = atob(arr[1]),
        n = bstr.length,
        u8arr = new Uint8Array(n);
      while (n--) {
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new Blob([u8arr], { type: mime });
    },
    uploadImg(dataURL, file, index, length) {
      // let size = file.size / 1024 / 1024;
      // if (size > 50) {
      //   // 2M
      //   this.$toast.fail("50M");
      //   this.loading.clear();
      //   return;
      // }
      let blob = this.dataURItoBlob(dataURL),
        formData = new FormData(),
        config = {
          headers: { "Content-Type": "multipart/form-data" }
        };
      formData.append("file", blob, file.name);
      formData.append("path", 1);
      this.$axios
        .post(
          this.$config.host + "/api/common/upload/file",
          formData,
          config
        )
        .then(res => {
          if (index + 1 == length) {
            this.loading.clear();
          }
          if (res.code == 0) {
            if (this.type == "1") {
              this.detail.gimg = res.data.filename;
            } else {
              this.picList.push(res.data.filename);
            }
          } else {
            this.$toast.fail("");
          }
        })
        .catch(err => {
          this.$toast.clear();
          console.log(err);
          this.$toast.fail("");
        });
    },
    submit() {
      if (!this.detail.gname) {
        this.$toast("");
        return;
      }
      // if (!this.detail.brandName) {
      //   this.$toast("");
      //   return;
      // }
      // if (!this.detail.guigeName) {
      //   this.$toast("");
      //   return;
      // }
      if (!this.detail.gmarketPrice) {
        this.$toast("");
        return;
      }
      if (!this.detail.goutPrice) {
        this.$toast("");
        return;
      }
      if (!this.detail.gcounts) {
        this.$toast("");
        return;
      }
      if (!this.detail.gsort) {
        this.$toast("");
        return;
      }
      if (!this.detail.gimg) {
        this.$toast("");
        return;
      }
      if (!this.detail.unitCode) {
        this.$toast("");
        return;
      }
      if (!this.detail.cateCode) {
        this.$toast("");
        return;
      }
      const loading = this.$toast.loading({
        duration: 0, //  toast
        forbidClick: true // 
      });
      editShopGood({
        gcode: this.detail.gcode || undefined,
        gname: this.detail.gname,
        brandName: this.detail.brandName,
        guigeName: this.detail.guigeName,
        gmarketPrice: this.detail.gmarketPrice,
        goutPrice: this.detail.goutPrice,
        gcounts: this.detail.gcounts,
        gsort: this.detail.gsort,
        gimg: this.detail.gimg,
        gdes: this.detail.gdes,
        unitCode: this.detail.unitCode,
        cateCode: this.detail.cateCode,
        gmsg: this.picList.join(",")
      }).then(res => {
        loading.clear();
        if (res.code == 0) {
          this.$toast(this.$route.query.type == "1" ? "" : "");
          setTimeout(() => {
            this.$router.back();
          });
        } else {
          this.$toast(res.msg);
        }
      });
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, //  toast
        forbidClick: true // 
      });
      getShopCateList().then(res => {
        loading.clear();
        if (res.code == 0) {
          this.cateList = res.data;
        } else {
          this.$toast(res.msg);
        }
      });
      getUnitList().then(res => {
        loading.clear();
        if (res.code == 0) {
          this.unitList = res.data;
        } else {
          this.$toast(res.msg);
        }
      });
    }
  },
  mounted() {
    this.loadData();
    if (this.$route.query.type == "2") {
      this.detail = JSON.parse(sessionStorage.getItem("goodDetail"));
    } else {
      this.detail = {
        gname: "",
        brandName: "",
        guigeName: "",
        gmarketPrice: "",
        goutPrice: "",
        gcounts: "",
        gsort: "",
        gimg: "",
        gdes: "",
        unitCode: "",
        cateCode: ""
      };
    }
  }
};
</script>
<style lang="scss" scoped>
.addGood {
  .mid {
    background: #fff;
    padding: 10px 0;
    .headPic {
      width: 200px;
      margin: 5px auto 15px;
      position: relative;
      .pic {
        width: 200px;
        max-height: 150px;
      }
      .icon {
        position: absolute;
        width: 25px;
        height: 25px;
        right: 0;
        bottom: 0;
      }
    }
    .content {
      display: flex;
      align-items: center;
      p {
        color: #333;
        flex: 2;
      }
      .icon {
        width: 20px;
        height: 20px;
        margin-left: 5px;
      }
    }
    .filesList {
      padding: 10px 15px;
      background: #fff;
      .filesItem {
        position: relative;
        border-bottom: 1pt solid #efefef;
        width: 30%;
        display: inline-block;
        margin-right: 3%;
        img {
          width: 100%;
        }
        .del {
          position: absolute;
          border: 1pt solid red;
          color: red;
          border-radius: 50%;
          width: 15px;
          height: 15px;
          text-align: center;
          line-height: 15px;
          right: -8px;
          top: -8px;
        }
      }
      p {
        word-break: break-all;
        line-height: 30px;
        margin: 0 30px 0 0;
      }
      .del {
        position: absolute;
        border: 1pt solid red;
        color: red;
        border-radius: 50%;
        width: 15px;
        height: 15px;
        text-align: center;
        line-height: 15px;
        right: 0;
        top: 5px;
      }
    }
    .submit {
      width: 90%;
      margin: 10px auto 0;
      text-align: center;
      background: $base-color;
      color: white;
      height: 45px;
      line-height: 45px;
      border-radius: 10px;
      margin-bottom: 10px;
    }
  }
}
</style>
<style lang="scss">
.addGood {
  .van-picker__cancel,
  .van-picker__confirm {
    color: #1989fa !important;
  }
  .van-cell__value {
    flex: 2;
  }
}
</style>